<template>
    <nav class="level">
        <div class="level-left" v-if="props.left_slots">
            <div class="level-item" v-for="name in props.left_slots">
                <!--
                    @slot The dynamically produced slot for each left slot
                -->
                <slot :name="name"/>
            </div>
        </div>
        <div class="level-right" v-if="props.right_slots">
            <div class="level-item" v-for="name in props.right_slots">
                <!--
                    @slot The dynamically produced slot for each right slot
                -->
                <slot :name="name"/>
            </div>
        </div>
        <template v-if="props.slots">
            <div class="level-item" v-for="name in props.slots">
                <!--
                    @slot The dynamically produced slot for each slot
                -->
                <slot :name="name"/>
            </div>
        </template>
    </nav>
</template>

<script lang="ts" setup>
/**
 * A multi-purpose horizontal level, which can contain almost any other element
 * 
 * @displayName Level
 * @link https://bulma.io/documentation/layout/level/
 */

    const props = defineProps<{
        left_slots?:string[],
        right_slots?:string[],
        slots?:string[]
    }>();
</script>